import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-button';
import '@servicenow/now-loader';
import { t } from 'sn-translate';
import { ADD_BUTTON, CANCEL_BUTTON } from '../../common/constants';
import '../list-wrapper';

const SNMultiRecordAssociatorContent = state => {
	const { loading, selectedRecordCount, itemFilterQuery, properties } = state;
	const {
		columns,
		disableHeader,
		hideSelectAll,
		label,
		userGivenTable,
		view,
		workspaceConfigId
	} = properties;

	const addDisabledAttr = selectedRecordCount === 0 || loading;
	const selectedRecordString =
		selectedRecordCount === 0 ? '' : ` (${selectedRecordCount})`;
	const selectAllBoolean = hideSelectAll === true || hideSelectAll === 'true';

	return (
		<Fragment>
			<sn-list-wrapper
				columns={columns}
				disableHeader={disableHeader}
				hideSelectAll={selectAllBoolean}
				itemFilterQuery={itemFilterQuery}
				label={t(label)}
				userGivenTable={userGivenTable}
				view={view}
				workspaceConfigId={workspaceConfigId}
			></sn-list-wrapper>

			<div className="footer-content">
				<div className="footer-content-text">
					<span>
						{selectedRecordCount} {t('items selected')}
					</span>
				</div>
				<div>
					<now-button
						id="cancel-button"
						label={t('Cancel')}
						variant="secondary"
						append-to-payload={{ button: CANCEL_BUTTON }}
					></now-button>
					<div className="spacer"></div>
					<now-button
						id="add-button"
						disabled={addDisabledAttr}
						label={t('Add') + selectedRecordString}
						variant="primary"
						append-to-payload={{ button: ADD_BUTTON }}
					>
						{loading ? <now-loader /> : undefined}
					</now-button>
				</div>
			</div>
		</Fragment>
	);
};

export default SNMultiRecordAssociatorContent;
