import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import style from '../../common/view.scss';
import { t } from 'sn-translate';
import {
	DEFAULT_EP,
	MANY_TO_MANY,
	MRA_ADD_SELECTED_SUCCEEDED,
	NOW_MODAL_OPENED_SET,
	REFRESH_REQUESTED,
	MRA_CANCEL_REQUESTED
} from '../../common/constants';
import '../mra-content';

const view = state => {
	const { showModal, properties } = state;
	const {
		columns,
		componentId,
		disableHeader,
		extensionPoint,
		hideSelectAll,
		label,
		parentFieldName,
		parentRecordSysId,
		referencedFieldName,
		table,
		type,
		userGivenTable,
		view,
		workspaceConfigId
	} = properties;

	return (
		<Fragment>
			<now-modal
				component-name={`${componentId}_modal`}
				opened={showModal}
				manage-opened
				size="lg"
				header-label={t('Add ' + label)}
				shouldFocusOnOpen={true}
			>
				<sn-multi-record-associator-content
					columns={columns}
					disableHeader={disableHeader}
					extensionPoint={extensionPoint}
					hideSelectAll={hideSelectAll}
					label={label}
					parentFieldName={parentFieldName}
					parentRecordSysId={parentRecordSysId}
					referencedFieldName={referencedFieldName}
					table={table}
					type={type}
					userGivenTable={userGivenTable}
					view={view}
					workspaceConfigId={workspaceConfigId}
				/>
			</now-modal>
		</Fragment>
	);
};

createCustomElement('sn-multi-record-associator', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		showModal: true
	},
	properties: {
		columns: { default: '' },
		componentId: { default: '' },
		disableHeader: { default: true },
		extensionPoint: { default: DEFAULT_EP },
		label: { default: '' },
		parentFieldName: { default: '' },
		parentRecordSysId: { default: '' },
		referencedFieldName: { default: '' },
		table: { default: '' },
		type: { default: MANY_TO_MANY },
		userGivenTable: { default: '' },
		view: { default: 'Default' },
		hideSelectAll: { default: false },
		workspaceConfigId: { default: '7b24ceae5304130084acddeeff7b12a3' }
	},
	styles: style,
	actionHandlers: {
		[NOW_MODAL_OPENED_SET]: {
			effect: ({ updateState, action }) => {
				updateState({
					showModal: action.payload.value
				});
			},
			stopPropagation: true
		},
		[MRA_ADD_SELECTED_SUCCEEDED]: {
			effect: ({ updateState }) => {
				updateState({
					showModal: false
				});
			},
			interceptors: [
				{
					after: context => {
						const { coeffects, effects } = context;
						return {
							coeffects,
							effects: [
								...effects,
								{
									effect: coeffects => {
										const { dispatch } = coeffects;
										dispatch(REFRESH_REQUESTED);
									},
									args: [coeffects]
								}
							]
						};
					}
				}
			],
			stopPropagation: true
		},
		[MRA_CANCEL_REQUESTED]: {
			effect: ({ dispatch }) => {
				dispatch(MRA_ADD_SELECTED_SUCCEEDED);
			},
			stopPropagation: true
		}
	}
});
