import { createHttpEffect } from '@servicenow/ui-effect-http';
import { get } from 'lodash';
import {
	ADD_BUTTON,
	CANCEL_BUTTON,
	ADD_ITEM_API,
	LOAD_ITEMS_FILTERS,
	UPDATE_ITEM_FILTER_QUERY,
	ADD_SELECTED_ITEM,
	LIST_SELECTION_COUNT_UPDATED,
	SELECT_ALL_RECORDS,
	NOW_BUTTON_CLICKED,
	UPDATE_ADD_LOADING,
	MRA_ADD_SELECTED_SUCCEEDED,
	MRA_CANCEL_REQUESTED,
	MANY_TO_MANY
} from '../../common/constants';

const addSelectedItemEffect = createHttpEffect(
	'/api/now/related_list_edit/create',
	{
		method: 'POST',
		dataParam: 'requestData',
		successActionType: MRA_ADD_SELECTED_SUCCEEDED
	}
);

const loadItemFiltersEffect = createHttpEffect(
	'/api/now/related_list_item_filter/getFilterQuery',
	{
		method: 'GET',
		queryParams: [
			'parentRecordSysId',
			'tableName',
			'parentFieldName',
			'referencedFieldName',
			'extensionPoint'
		],
		successActionType: UPDATE_ITEM_FILTER_QUERY
	}
);

const updateItemFilterQueryEffect = ({ updateState, state, action }) => {
	const getDefaultFilterQuery = properties => {
		const {
			parentRecordSysId,
			table,
			parentFieldName,
			referencedFieldName,
			type
		} = properties;
		if (type === MANY_TO_MANY) {
			return `^RLQUERY${table}.${referencedFieldName},=0,m2m^${parentFieldName}=${parentRecordSysId}^ENDRLQUERY`;
		}
		return `${parentFieldName}ISEMPTY^sys_id!=${parentRecordSysId}`;
	};
	const filterQuery = get(
		action,
		'payload.result.filterQuery',
		getDefaultFilterQuery(state.properties)
	);
	updateState({
		itemFilterQuery: filterQuery
	});
};

const addSelectedItem = ({ dispatch, state }) => {
	const {
		parentFieldName,
		referencedFieldName,
		userGivenTable,
		type,
		table,
		parentRecordSysId
	} = state.properties;
	const newItemsQuery = state.newItemsQuery;
	const requestData = {
		newItemsQuery,
		userGivenTable,
		parentRecordSysId,
		parentFieldName,
		referencedFieldName,
		m2mTableName: table,
		type
	};
	dispatch(UPDATE_ADD_LOADING, true);
	dispatch(ADD_ITEM_API, { requestData: requestData });
};

const updateSelectedRecordsCountEffect = ({ updateState, action }) => {
	const { selectionCount, query } = action.payload;
	updateState({
		selectedRecordCount: selectionCount,
		newItemsQuery: query
	});
};

const updateAddLoadingEffect = ({ updateState, action }) => {
	updateState({
		loading: action.payload
	});
};

const dispatchButtonClicked = coeffects => {
	const { dispatch, action } = coeffects;
	if (action.payload.button === ADD_BUTTON) {
		dispatch(ADD_SELECTED_ITEM);
	} else if (action.payload.button === CANCEL_BUTTON) {
		dispatch(MRA_CANCEL_REQUESTED);
	}
};

export const actionHandlers = {
	[NOW_BUTTON_CLICKED]: {
		effect: dispatchButtonClicked,
		stopPropagation: true
	},
	[ADD_SELECTED_ITEM]: {
		effect: addSelectedItem,
		stopPropagation: true
	},
	[ADD_ITEM_API]: {
		...addSelectedItemEffect,
		stopPropagation: true
	},
	[LOAD_ITEMS_FILTERS]: {
		...loadItemFiltersEffect,
		stopPropagation: true
	},
	[UPDATE_ITEM_FILTER_QUERY]: {
		effect: updateItemFilterQueryEffect,
		stopPropagation: true
	},
	[LIST_SELECTION_COUNT_UPDATED]: {
		effect: updateSelectedRecordsCountEffect,
		stopPropagation: true
	},
	[SELECT_ALL_RECORDS]: {
		effect: updateSelectedRecordsCountEffect,
		stopPropagation: true
	},
	[UPDATE_ADD_LOADING]: {
		effect: updateAddLoadingEffect,
		stopPropagation: true
	}
};
