import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import style from '../../common/view.scss';
import view from './view';
import { actionHandlers } from './actions';
import {
	DEFAULT_EP,
	LOAD_ITEMS_FILTERS,
	MANY_TO_MANY,
	UPDATE_ITEM_FILTER_QUERY
} from '../../common/constants';

createCustomElement('sn-multi-record-associator-content', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		loading: false,
		newItemsQuery: '',
		selectedRecordCount: 0
	},
	properties: {
		columns: { default: '' },
		disableHeader: { default: true },
		extensionPoint: { default: DEFAULT_EP },
		hideSelectAll: { default: false },
		label: { default: '' },
		parentFieldName: { default: '' },
		parentRecordSysId: { default: '' },
		referencedFieldName: { default: '' },
		table: { default: '' },
		type: { default: MANY_TO_MANY },
		userGivenTable: { default: '' },
		view: { default: 'Default' },
		workspaceConfigId: { default: '7b24ceae5304130084acddeeff7b12a3' }
	},
	onBootstrap(host, dispatch) {
		if (host.extensionPoint && host.extensionPoint !== DEFAULT_EP) {
			dispatch(LOAD_ITEMS_FILTERS, {
				parentRecordSysId: host.parentRecordSysId,
				tableName: host.table,
				parentFieldName: host.parentFieldName,
				referencedFieldName: host.referencedFieldName,
				extensionPoint: host.extensionPoint,
				type: host.type
			});
		} else {
			dispatch(UPDATE_ITEM_FILTER_QUERY);
		}
	},
	styles: style,
	actionHandlers
});
