import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import style from '../../common/view.scss';
import view from './view';
import { actionHandlers } from './actions';

export const SNListWrapper = 'sn-list-wrapper';

createCustomElement(SNListWrapper, {
	renderer: {
		type: snabbdom,
		view
	},

	properties: {
		userGivenTable: { default: 'sn_install_base_item' },
		label: { default: '' },
		view: { default: 'Default' },
		columns: { default: '' },
		itemFilterQuery: { default: undefined },
		disableHeader: { default: true },
		hideSelectAll: { default: false },
		timestamp: {},
		listUpdated: {}
	},

	styles: style,
	actionHandlers
});
